<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full">
                            <h4 class="modal-title">{{ $t('ProjectDescription') }}</h4>
                            <button type="button" class="close" @click="Close">
                                <i class="fa fa-times" aria-hidden="true"></i>
                            </button>
                        </div>
                        <div class="modal-body" style="max-height: 400px; overflow-x: auto;">
                            <div class="listing">
                                <div class="table-responsive tableborder">
                                    <table class="table-sorting table table-bordered mb-0">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th width="20%">
                                                    <span id="NAME">{{ $t('th_project_projectname') }}</span>
                                                </th>
                                                <th width="20%">
                                                    <span id="HOURLY_RATE">{{ $t('th_project_perhour') }} {{ Currency }}</span>
                                                </th>
                                                <th width="15%">
                                                    <span id="TOTAL_HOURS">{{ $t('th_project_total_hours') }}</span>
                                                </th>
                                                <th width="15%">
                                                    <span id="HOURS_SPENT">{{ $t('TimeTracked') }}</span>
                                                </th>
                                                <th width="15%">
                                                    <span id="TOTAL_LEFT">{{ $t('th_project_left_hours') }}</span>
                                                </th>
                                                <th width="15%">
                                                    <span id="PROJECT_STATUS">{{ $t('Status') }}</span>
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-if="projectDetails.length === 0">
                                                <td colspan="6" class="text-center text-danger no-record">{{ $t('NoRecordfound') }}</td>
                                            </tr>
                                            <tr v-for="project in projectDetails" :key="project.id">
                                                <td title="project.PROJECT_NAME">
                                                    {{ truncate(project.PROJECT_NAME, 15) }}
                                                </td>
                                                <td>
                                                    <span title="project.BASE_RATE">{{ project.BASE_RATE }}</span>
                                                </td>
                                                <td>
                                                    {{ project.ESTIMATED_HOURS > 0 ? project.ESTIMATED_HOURS + 'H' : '0H' }}
                                                </td>
                                                <td>
                                                    {{ convertToTimeFormat(project.HOURSSPENT) }}
                                                </td>
                                                <td>
                                                    {{ (project.ESTIMATED_HOURS * 3600) - project.HOURSSPENT > 0 
                                                        ? convertToTimeFormat((project.ESTIMATED_HOURS * 3600) - project.HOURSSPENT) 
                                                        : 'NA' }}
                                                </td>
                                                <td>
                                                    {{ project.STATUS_NAME }}
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
    </template>
    
    <script>
    import DataServices from "../../services/DataService";
    
    export default {
        props: {
            ClientId: {
                type: [String, Number],
                required: true
            }
        },
        data() {
            return {
                Currency: '',
                projectDetails: []
            };
        },
        created() {
            this.Currency = this.GetUserInfo.Currency;
            this.getForm();
        },
        methods: {
            async getForm() {
                const params = `clientId=${this.ClientId}`;
                try {
                    const response = await DataServices.GetProjectDetail(params);
                    this.projectDetails = response.data;
                } catch (error) {
                    console.error("Error fetching project details:", error);
                }
            },
            Close: function () {
                var vm = this;
                vm.$parent.ProjectDetailPopup = false;
            },
            truncate(text, length) {
                return text.length > length ? text.substring(0, length) + '...' : text;
            },
            convertToTimeFormat(seconds) {
                const hours = Math.floor(seconds / 3600);
                const minutes = Math.floor((seconds % 3600) / 60);
                return `${hours}H ${minutes}M`;
            }
        }
    };
    </script>
    