<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list v-show="IsAccountView" :IsShowAction="true" ModuleName="Company" SubModuleCode="Client"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" IdentityColumn="USER_ID"
            
            :HeaderText="$t('Accounts') + '/' + $t('Clients')" :listType="['List']" :ListData="ClientListing" :HeaderData="Headers"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields"
            :ListDataCallBackFunction="FetchRecords" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                    <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'status_id')"
                        v-if="data.row.STATUS_NAME == 'Active'">
                        {{ $t(data.row.STATUS_NAME) }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'status_id')"
                        v-if="data.row.STATUS_NAME == 'Inactive'">
                        {{ $t(data.row.STATUS_NAME) }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'status_id')"
                        v-if="data.row.STATUS_NAME == 'ActivationAwaited'">
                        {{ $t('ActivationAwaited') }}   {{ $t('NotVerified') }}
                        <!-- {{ 'Awaiting Activation / Not Verified' }} -->
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'status_id')"
                        v-if="data.row.STATUS_NAME == 'Pending'">
                        {{ $t('ActivationPending') }}   {{ $t('NotVerified') }}
                        <!-- {{ 'Activation e-mail pending / Not Verified' }} -->
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'TOTAL_PROJECT'">
                    <a id="ancProjectDetail" @click="ProjectDetail(data.row)" class="text-dark" href="javascript:;"><i
                            class="fa fa-file-text-o" aria-hidden="true"></i><span
                            class="noti-circle noti-blue">{{ data.row.TOTAL_PROJECT }}</span></a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'CLIENT_NAME'">
                    <RouterLink v-if="data.row.USER_ID == data.row.PARENT_ID"
                        :to="'/Client/UpdateAccount/' + data.row.USER_ID" :title="data.row[data.column.COLUMN_NAME]"
                        data-toggle-tooltip="tooltip" data-original-title="Click here to Update Account">
                        {{ data.row[data.column.COLUMN_NAME] }}
                    </RouterLink>
                    <span v-if="data.row.USER_ID == data.row.PARENT_ID" style="margin-left: 5px;">( {{ $t('Client') }} <span class="noti-circle noti-blue ml-1 noti-clients">{{
                        data.row.TOTALCHILD }}</span> )</span>
                    <RouterLink v-if="data.row.USER_ID != (data.row.PARENT_ID == null ? 0 : data.row.PARENT_ID)"
                                :to="'/Client/Update/' + data.row.USER_ID + '/' + (data.row.PARENT_ID == null ? 0 : data.row.PARENT_ID)"
                                :title="data.row[data.column.COLUMN_NAME]" 
                                data-toggle-tooltip="tooltip" 
                                data-original-title="Click here to Update Client">
                                {{ data.row[data.column.COLUMN_NAME] }}
                    </RouterLink>
                </template>
            </template>
            <template v-slot:headerleftbuttons="{ data }">
                    <div class="col-sm-12 col-md-2 col-lg-2 float-left mt-2">
                                <select v-model="clientViewType"  name="ddlClientViewType" class="form-control customized_ddl" v-on:change="clientViewList()">
                                    <option value="accounts">Accounts with Clients</option>
                                    <option value="clients">Clients Only</option>
                                </select>
                    </div>
                </template>
        </tg-list>
        <DynamicClientListing v-if="isDynamicClientListing" @switchToAccounts="switchToAccounts"/>
        <ProjectDetailModal v-if="ProjectDetailPopup" :ClientId="ClientId"></ProjectDetailModal>
        <ConfigurationModal v-if="ConfigurationPopup" :ClientId="ClientId"></ConfigurationModal>
    </section>
</template>
<script>
import Schema from "../../views/Client/ClientSchema.js";
import DataServices from "../../services/DataService";
import ProjectDetailModal from '../../components/Modals/ProjectDetailModal.vue';
import ConfigurationModal from '../../components/Modals/ConfigurationModal.vue';
import DynamicClientListing from "./DynamicClientListing.vue";
export default {
    components: { ProjectDetailModal, ConfigurationModal,DynamicClientListing },
    data() {
        return {
            isLoading: false,
            noRecords: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            ClientId: 0,
            ProjectDetailPopup: false,
            ConfigurationPopup: false,
            listActions: Schema.listActions,
            Headers: [{
                "COLUMN_NAME": "CLIENT_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("AccountName"),
                "DISPLAY_ORDER": 1,
                
            },
            {
                "COLUMN_NAME": "ACCOUNT_NO",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("AccountNo"),
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "TOTAL_PROJECT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("CAProjects"),
                "DISPLAY_ORDER": 3,
            },
            {
                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Status"),
                "DISPLAY_ORDER": 4,
            }
            ],
            SubHeaders: [{
                "COLUMN_NAME": "CLIENT_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("ClientName"),
                "DISPLAY_ORDER": 1,
                settings: {
                    isInSlot: true,
                }
            },
            {
                "COLUMN_NAME": "TOTAL_PROJECT",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("CAProjects"),
                "DISPLAY_ORDER": 2,
                settings: {
                    isInSlot: true,
                }
            },
            {
                "COLUMN_NAME": "USER_PHONE",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Phone"),
                "DISPLAY_ORDER": 3,
            },
            {
                "COLUMN_NAME": "CONTRACT_NO",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("ContractNo"),
                "DISPLAY_ORDER": 4,
            },
            {
                "COLUMN_NAME": "EMAIL_ID",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Email"),
                "DISPLAY_ORDER": 5,
            },
            {
                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Status"),
                "DISPLAY_ORDER": 6,
                settings: {
                    isInSlot: true,
                }
            }
            ],
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalPages: 0,
            ClientListing: [],
            GroupStart: 1,
            GroupEnd: 3,
            user_type: null,
            user_id: null,
            AccountName: '',
            ClientName: '',
            SearchByEmail: '',
            SearchCondition: '',
            IsAccountView: true,
            CheckExtraFeatures: false,
            StatusIds: '',
            Isdeleted: '',
            SearchByAccountNo:'',
            SearchByContractNo : '',
            SearchByPhoneNo :'',
            clientViewType:'accounts',
            isDynamicClientListing:false
        }

    },
    created: async function () {
        var vm = this;
        this.user_id = this.GetUserInfo.ID;
       
        this.user_type = this.GetUserInfo.UserType;
        this.companyId = this.GetUserInfo.CompanyId;
        await Schema.bindleftCommonSearchdropdown(vm, "Company", "Client");
        await this.CheckFeaturesForCompany();
        await this.FetchRecords();
    },
    methods: {
        clientViewList() {
            if (this.clientViewType === "clients") {
                this.IsAccountView = false;
                this.isDynamicClientListing = true;
            }
        },
        switchToAccounts() {
            this.isDynamicClientListing = false;
            this.IsAccountView = true;
            this.clientViewType = "accounts";
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Inactive':
                        if (row.STATUS_ID == 1001) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'Active / Verified':
                        if (row.STATUS_ID == 1002) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'Awaiting Activation/ Not Verified':
                        if (row.USER_ID != row.PARENT_ID && row.STATUS_CODE == 'ACT_AWAITED') {
                            action.isVisible = false;
                            action.isDisabled = true;
                        } else {
                            action.isVisible = false;
                            action.isDisabled = false;
                        }
                        break;
                    case 'Activation e-mail pending/ Not Verified':
                        if (row.USER_ID != row.PARENT_ID && row.STATUS_CODE == 'CL_PEN') {
                            action.isVisible = true;
                            action.isDisabled = true;
                        } else {
                            action.isVisible = false;
                            action.isDisabled = false;
                        }
                        break;

                    case 'Send/Resend Activation Mail':
                        if (row.USER_ID != row.PARENT_ID && row.STATUS_ID != 1001) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'Add Quotes':
                        if (row.USER_ID != row.PARENT_ID && row.STATUS_ID == 1001) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'Generate/ Reset Password':
                        if (row.USER_ID != row.PARENT_ID && row.STATUS_ID == 1001) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'Configuration':
                        if (row.USER_ID != row.PARENT_ID) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        ProjectDetail: function (item) {
            var vm = this;
            if (item == undefined) {
                vm.ClientId = 0;
            } else {
                vm.ClientId = item.USER_ID;
            }
            vm.ProjectDetailPopup = !vm.ProjectDetailPopup;
        },
        actionButtonClick(callbackfunction, item, index, event, isChild) {
            switch (callbackfunction) {
                case 'Inactive':
                    this.UpdateStatusAccount(item, 1002);
                    break;
                case 'ActiveOrVerified':
                    this.UpdateStatusAccount(item, 1001);
                    break;
                case 'ActivationMail':
                    this.SendActivationMail(item.USER_ID, 'Client');
                    break;
                case 'Configuration':
                    this.ClientConfiguration(item.USER_ID);
                    break;
                case 'Generateresetpassword':
                    this.SendActivationMail(item.USER_ID, 'Client');
                    break;
                case 'AddQuotes':
                    this.AddQuotes(item.USER_ID);
                    break;
                case 'Edit':
                    this.UpdateAccount(item);
                    break;
            }
        },
        AddQuotes: async function (ClientId) {
            var vm = this;
            vm.$router.push({path: `/CRM/ManageQuotation/0/CRM/CRM_QUOTES/0?clientId=${ClientId}`});
            //window.location.href = "/Quotation/Create/0?code=" + 3 + "&clientId=" + ClientId + "";
        },
        openaddAccount: async function (ClientId) {
            var vm = this;
            window.location.href = "/CRM/ManageAccount?id=0&AccountId=" + 1 + "&AccountCode=NEW_CUSTOMER&reqfrom=/client/Index";
        },
        openaddClient: async function (ClientId) {
            var vm = this;
            this.$router.push({ name: 'AddClient' });
        },
        UpdateAccount: async function (item) {
            var vm = this;
            if (item.USER_ID == item.PARENT_ID) {
                vm.$router.push({ name: 'UpdateAccount', params: { id: item.USER_ID } });
            } else {
                if (item.PARENT_ID == null) {
                    item.PARENT_ID = 0;
                }
                vm.$router.push({ name: 'UpdateClient', params: { id: item.USER_ID, parentId: item.PARENT_ID } });
            }
        },
        UpdateStatusAccount: async function (item, status) {
            var vm = this;
            await vm.SendActivationMail(item.USER_ID, 'UpdateStatus');
            var type = item.USER_ID == item.PARENT_ID ? 'Account' : 'Client';
            var params = `id=${item.USER_ID}&statusId=${status}&type=${type}`;
            await DataServices.UpdateStatusAccount(params).then(response => {
                if (response.status == 200) {
                    vm.FetchRecords();
                    vm.ShowAlert(vm.$t('ConfigurationSuccessDeActivationMailToCL'), "Success", true, vm.$t('Alert'));
                }
                else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                }
            });
        },
        ClientConfiguration: function (UserId) {
            var vm = this;
            vm.ClientId = UserId;
            vm.ConfigurationPopup = !vm.ConfigurationPopup;
        },
        SendActivationMail: async function (UserId, type) {
            var vm = this;
            var params = `ClientId=${UserId}&from=${type}`;
            await DataServices.SendActivationMail(params).then(response => {
                if (response.status == 200) {
                    if (type == 'Client') {
                        vm.FetchRecords();
                        vm.ShowAlert(vm.$t('SuccessActivationMailToCL'), "Success", true, vm.$t('Alert'));
                    }else if(type == 'UpdateStatus'){}
                    else {
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                    }
                }
            });
        },
        extractDynamicValues: function (searchCondition) {
            var vm = this;
            let likeRegex = /(\w+)\s+LIKE\s+'%([^%]+)%'/g;
            let inRegex = /(\w+)\s+in\s+\(([^)]+)\)/g;
            let matches;
            while ((matches = likeRegex.exec(searchCondition)) !== null) {
                let field = matches[1];
                let value = matches[2].trim();
                if (field === 'Account') {
                    vm.AccountName = value;
                } else if (field === 'ClientName') {
                    vm.ClientName = value;
                }
                else if (field === 'Email') {
                    vm.SearchByEmail = value;
                }
                else if (field === 'STATUS_ID') {
                    vm.StatusIds = values;
                }
                else if (field === 'AccountNo') {
                    vm.SearchByAccountNo = value;
                }
                else if (field === 'ContractNo') {
                    vm.SearchByContractNo = value;
                }
                else if (field === 'PhoneNo') {
                    vm.SearchByPhoneNo = value;
                }
            }
            while ((matches = inRegex.exec(searchCondition)) !== null) {
                let field = matches[1];
                let values = matches[2].split(',').map(v => v.replace(/'/g, '').trim());
                if (field === 'STATUS_ID') {
                    vm.StatusIds = values;
                }
            }
            return vm;
        },
        leftCommonSearch: function (value) {
            var vm = this;
            vm.AccountName = '';
            vm.ClientName = '';
            vm.SearchByEmail = '';
            vm.StatusIds = '';
            vm.SearchByAccountNo = '';
            vm.SearchByContractNo = '';
            vm.SearchByPhoneNo = '';
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            var searchCondition = value.searchCondition;
            if (typeof value.searchCondition != 'undefined') {
                vm.extractDynamicValues(searchCondition);
            }
            this.FetchRecords();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "openaddAccount":
                    this.openaddAccount(null);
                    break;
                case "openaddClient":
                    this.openaddClient();
                    break;
            }
        },
        commonDeleteData: async function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                var params = `clientIds=${val}`;
                var childUserIds = '';
                var parentAccountIds = '';
                await DataServices.GetClientdetail(params).then(function (response) {
                    childUserIds = response.data[0].childUserIds;
                    parentAccountIds = response.data[1].parentAccountIds;
                });
                await vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete")), async function () {
                    if (childUserIds) {
                        var url = `ids=${val}&tableName=TALYGEN_company_client&primaryKey=user_id`;
                        await DataServices.Delete(url).then(response => {
                            if (response.status == 200) {
                                vm.Isdeleted = true;
                            }
                            else {
                                vm.Isdeleted = false;
                            }
                        });
                    }
                    if (parentAccountIds) {
                        var url = `ids=${val}&tableName=TALYGEN_account&primaryKey=account_id`;
                        await DataServices.Delete(url).then(response => {
                            if (response.status == 200) {
                                vm.Isdeleted = true;
                            }
                            else {
                                vm.Isdeleted = false;

                            }
                        });
                    }
                    if (vm.Isdeleted == true) {
                        vm.FetchRecords();
                        vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Account')]), "Success", true, vm.$t('Alert'));
                    } else {
                        vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Account')]), "failure", true, vm.$t('Alert'));
                    }
                });

            }
            else {
                vm.ShowAlert(vm.$t('Selectanyrecordtodelete', [vm.$t('Account')]), "failure", true, vm.$t('Alert'));
            }
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        sortdata: function (sortBy) {
            let sortField = sortBy === 'CLIENT_NAME' ? 'NAME' : sortBy;
            this.SortBy = sortField;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortField;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        onExpandChild: function (item) {
            var vueObj = this;
            vueObj.isLoading = true;
            var url = `parentId=${item.PARENT_ID}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&searchByName=${vueObj.AccountName}&searchBy=${vueObj.ClientName}&statusIds=${vueObj.StatusIds}&searchByEmail=${vueObj.SearchByEmail}&searchByAccountNo=${vueObj.SearchByAccountNo}&searchByContractNo=${vueObj.SearchByContractNo}&searchByPhoneNo=${vueObj.SearchByPhoneNo}`;
            DataServices.GetClientListing(url)
                .then(function (response) {
                    if (response.data != "" && response.data != null) {
                        if (response.data.length > 0) {
                            //vueObj.Headers = vueObj.SubHeaders;                                
                            item.child.headers = vueObj.SubHeaders;
                            if (vueObj.CheckExtraFeatures == true) {
                                item.child.headers[1].VISIBLE = false;
                            } else {
                                item.child.headers[1].VISIBLE = true;
                            }

                            item.child.data = response.data;
                            $.each(item.child.data, function (index, item) {
                                item.isCheckBoxDisabled = false;
                                if (item.TOTAL_PROJECT > 0) {
                                    item.isCheckBoxDisabled = true;
                                }
                            });
                            vueObj.listheaderbuttons[2].isdisabled = true;
                            vueObj.noRecord = false;
                            vueObj.isLoading = false;
                        }
                        else {
                            vueObj.noRecord = true;
                            item.child.data = [];
                            vueObj.isLoading = false;
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        item.child.data = [];
                        vueObj.isLoading = false;
                    }
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                })
        },
        CheckFeaturesForCompany: async function () {
            var vm = this;
            var appendingUrl = `featureCode=${'USICBILLING'}`;
            await DataServices.CheckExtraFeatures(appendingUrl).then(function (response) {
                if (response.data == true) {
                    vm.CheckExtraFeatures = true;
                } else {
                    vm.CheckExtraFeatures = false;
                }
            });
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                case 'ActivationAwaited':
                    returnClass = 'px-2 text-center bg-warning text-white d-block bg-warning';
                    break;
                case 'Pending':
                    returnClass = 'px-2 text-center bg-primary text-white d-block bg-primary';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        FetchRecords: async function () {
            var vueObj = this;
            var url = `parentId=0&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&searchByName=${vueObj.AccountName}&searchBy=${vueObj.ClientName}&statusIds=${vueObj.StatusIds}&searchByEmail=${vueObj.SearchByEmail}&searchByAccountNo=${vueObj.SearchByAccountNo}&searchByContractNo=${vueObj.SearchByContractNo}&searchByPhoneNo=${vueObj.SearchByPhoneNo}`;
            await DataServices.GetClientListing(url)
                .then(function (response) {
                    if (response.data != "" && response.data != null) {
                        vueObj.Headers[1].VISIBLE = true; 
                        vueObj.Headers.forEach((column) => {
                            column.settings = null;
                            if (column.COLUMN_NAME == "STATUS_NAME" || column.COLUMN_NAME == "TOTAL_PROJECT" || column.COLUMN_NAME == "CLIENT_NAME") {
                                column.settings = {
                                    isInSlot: true,
                                };
                            }
                        });
                        if (response.data.length > 0) {
                            var updateArray = response.data;
                            updateArray.forEach((x) => {
                                x.child = {
                                    onExpandChild: vueObj.onExpandChild, 
                                    isShowChild: false,
                                    isShowExpandButton: true,
                                    headers: [],
                                    data: []
                                }
                            });
                            $.each(updateArray, function (index, item) {
                                var forms = "";
                                var arr = item.selectedForm;
                                $.each(arr, function (i, itm) {
                                    forms += (forms == "") ? itm.name : ", " + itm.name;
                                });
                                item.selectedForm = forms;
                            });
                            vueObj.ClientListing = updateArray;
                            vueObj.TotalRecords = response.data[0].TOTAL_RECORDS;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.listheaderbuttons[2].isdisabled = true;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.ClientListing = [];
                            vueObj.TotalRecords = 0
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.ClientListing = [];
                        vueObj.TotalRecords = 0
                    }
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                })
        }
    }
}
</script>

<style>
.parent-childrow {
    top: -23px;
    left: 17px;
}
</style>