<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list ref="tgList" v-show="IsClientView" :IsShowAction="true" ModuleName="Company" SubModuleCode="Client"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" IdentityColumn="user_id"
            :HeaderText="$t('Clients')" :listType="['List']" :ListData="ClientData" :HeaderData="ClientHeaders"
            :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :ViewList="ViewList" v-on:ApplyView="OnApplyView"
            :ListDataCallBackFunction="FetchClientDetails" :showCheckBox="true" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'Status_Name'">
                    <span v-bind:class="getStatusClassList(data.row.Status_Name, 'status_id')"
                        v-if="data.row.Status_Name == 'Active'">
                        {{ $t(data.row.Status_Name) }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.Status_Name, 'status_id')"
                        v-if="data.row.Status_Name == 'Inactive'">
                        {{ $t(data.row.Status_Name) }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.Status_Name, 'status_id')"
                        v-if="data.row.Status_Name == 'ActivationAwaited'">
                        {{ $t('ActivationAwaited') }}   {{ $t('NotVerified') }}
                    </span>
                    <span v-bind:class="getStatusClassList(data.row.Status_Name, 'status_id')"
                        v-if="data.row.Status_Name == 'Pending'">
                        {{ $t('ActivationPending') }}   {{ $t('NotVerified') }}
                    </span>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'owner_id'">
                    <td class="text-center single-action tourguide_a" v-if="data.row.owner_id != null">
                        {{ data.row.owner_id }}
                    </td>
                    <td class="text-center single-action tourguide_a" v-else>--</td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'customer_type_id'">
                    <td class="text-center single-action tourguide_a" v-if="data.row.customer_type_id != null">
                        {{ data.row.customer_type_id }}
                    </td>
                    <td class="text-center single-action tourguide_a" v-else>--</td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'industry_type_id'">
                    <td class="text-center single-action tourguide_a" v-if="data.row.industry_type_id != null">
                        {{ data.row.industry_type_id }}
                    </td>
                    <td class="text-center single-action tourguide_a" v-else>--</td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'user_phone'">
                    <td class="text-center single-action tourguide_a" v-if="data.row.user_phone != null">
                        {{ data.row.user_phone }}
                    </td>
                    <td class="text-center single-action tourguide_a" v-else>--</td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'jurisdiction_id'">
                    <td class="text-center single-action tourguide_a" v-if="data.row.jurisdiction_id != null">
                        {{ data.row.jurisdiction_id }}
                    </td>
                    <td class="text-center single-action tourguide_a" v-else>--</td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'timezone_id'">
                    <td class="text-center single-action tourguide_a" v-if="data.row.timezone_id != null">
                        {{ data.row.timezone_id }}
                    </td>
                    <td class="text-center single-action tourguide_a" v-else>--</td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'Customer_Name'">
                    <RouterLink v-if="data.row.user_id != (data.row.parent_id == null ? 0 : data.row.parent_id)"
                                :to="'/Client/Update/' + data.row.user_id + '/' + (data.row.parent_id == null ? 0 : data.row.parent_id)"
                                :title="data.row[data.column.COLUMN_NAME]" 
                                data-toggle-tooltip="tooltip" 
                                data-original-title="Click here to Update Client">
                                {{ data.row[data.column.COLUMN_NAME] }}
                    </RouterLink>
                </template>
            </template>
            <template v-slot:headerleftbuttons="{ data }">
                    <div class="col-sm-12 col-md-2 col-lg-2 float-left mt-2 px-1">
                                <select v-model="clientViewType"  name="ddlClientViewType" class="form-control customized_ddl" v-on:change="clientViewTypeList()">
                                    <option value="accounts">Accounts with Clients</option>
                                    <option value="clients">Clients Only</option>
                                </select>
                    </div>
                </template>
        </tg-list>
        <ProjectDetailModal v-if="ProjectDetailPopup" :ClientId="ClientId"></ProjectDetailModal>
        <ConfigurationModal v-if="ConfigurationPopup" :ClientId="ClientId"></ConfigurationModal>
        <ManageView v-if="ShowManageView" submodulecode="Client" modulename="Company" v-on:close="ShowManageView = false"
            v-bind:callbackfunction="FetchClientDetails" :usertype="usertype">
            <h5 slot="header">{{ $t('Search') }}</h5>
        </ManageView>
    </section>
</template>
<script>
import Schema from "../../views/Client/DynamicClientSchema.js";
import DataServices from "../../services/DataService";
import ProjectDetailModal from '../../components/Modals/ProjectDetailModal.vue';
import ConfigurationModal from '../../components/Modals/ConfigurationModal.vue';
import ManageView from '../../components/Common/view-layout.vue'
export default {
    components: { ProjectDetailModal, ConfigurationModal, ManageView },
    data() {
        return {
            isLoading: false,
            noRecords: false,
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            ClientId: 0,
            ProjectDetailPopup: false,
            ConfigurationPopup: false,
            listActions: Schema.listActions,
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalPages: 0,
            GroupStart: 1,
            GroupEnd: 3,
            user_type: null,
            user_id: null,
            AccountName: '',
            ClientName: '',
            SearchByEmail: '',
            SearchCondition: '',
            IsClientView: true,
            StatusIds: '',
            Isdeleted: '',
            SearchByAccountNo:'',
            SearchByContractNo : '',
            ClientData:[],
            SearchByPhoneNo :'',
            clientViewType:'clients',
            ClientHeaders:[],
            viewId: 0,
            ViewList: [],
            ViewListId:'',
            usertype: null,
            ShowManageView: false
        }
    },
    created: async function () {
        var vm = this;
        this.user_id = this.GetUserInfo.ID;
        this.usertype = this.GetUserInfo.UserType;
        this.companyId = this.GetUserInfo.CompanyId;
        await this.FetchClientDetails();
        await vm.GetManageViewList();
        await Schema.bindleftCommonSearchdropdown(vm, "Company", "Client");
    },
    methods: {
        ManageView: function () {
            var vm = this;
            vm.ShowManageView = true;
            setTimeout(function () {
                $("#modalManageView").modal('show');
                setTimeout(function () {
                    vm.SetBootstraptoolTip();
                }, 200);
            }, 1000);
        },
        GetManageViewList: async function () {
            var vm = this;
            vm.ViewList = []
            var url = `moduleName=Company&subModuleCode=Client&viewId=0&pageSize=10&pageNum=1&sortBy=&sortExp=`;
            await DataServices.GetViewList(url).then(function (response) {
                if (response.data && typeof (response.data.DATA) != 'undefined') {
                    if (vm.usertype != 'CL') {
                        vm.ViewList = response.data.DATA;
                    }
                }
                var chkVACTIVE = 0;
                vm.ViewListId = "0";
                if (vm.ViewList == null || vm.ViewList == "") {
                }
                else {
                    vm.ViewList.forEach(function (item) {

                        if (item.IS_SLECTED_USER_ID > 0) {
                            chkVACTIVE = chkVACTIVE + 1;
                            vm.ViewListId = item.VIEW_ID;
                        }
                    });
                }
                var indx = 0;
                if (chkVACTIVE > 0) {
                    vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 0, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                }
                else {
                    if (vm.ViewList == null || vm.ViewList == "") {

                        vm.ViewList.push({ "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });
                    }
                    else {
                        vm.ViewList.splice(indx, 0, { "SUB_MODULE_NAME": "", "SUB_MODULE_CODE": "", "VIEW_NAME": "Default View", "VIEW_ID": 0, "IS_APPLICABLE_TO_ALL": 1, "MODULE_NAME": "", "CREATED_AT": "", "IS_SLECTED_USER_ID": 1, "CAN_EDIT": 0, "ROWNUMBER": 0, "VIEW_TYPE": "", "USER_LIST": "" });

                    }
                }
                setTimeout(() => {
                    if (vm.ViewListId > 0) {
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = vm.ViewListId;
                    } else {
                        vm.$refs.tgList.$refs.tgListHeaderButtons.ViewListId = "0";
                    }
                }, 1000);
            });
        },
        DefaultView: function () {
            var vm = this;
            var obj = {
                ModuleName: 'Company',
                SubmoduleCode: 'Client'
            };
            var objectPostString = JSON.stringify(obj);
            DataServices.ClearView(objectPostString).then(function (response) {
                if (response.data != null) {
                    if (response.data.code == "1" || response.data.Code == "1") {
                        vm.ShowAlert(vm.$t("ClearSuccess", [vm.$t("DefaultView")]), 'success', true, vm.$t('Alert'));
                        vm.FetchClientDetails();
                    }
                    else {
                        vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), 'failure', true, vm.$t('Alert'));
                }
            });
        },
        OnApplyView(viewId) {
            var vm = this;
            if (viewId == 0) {
                vm.DefaultView()
            }
            else {
                var obj = {
                    ViewId: viewId,
                    ModuleName: 'Company',
                    SubmoduleCode: 'Client'
                };
                var objectPostString = JSON.stringify(obj);
                DataServices.ApplyView(objectPostString).then(function (response) {
                    if (response.data != null) {
                        if (response.data.code == "1" || response.data.Code == "1") {
                            vm.ShowAlert(vm.$t("ApplySuccess", [vm.$t("View")]), 'success', true, vm.$t('Alert'));
                            vm.FetchClientDetails();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                        }
                    }
                    else {
                        vm.ShowAlert(vm.$t("AddedError", [vm.$t("View")]), 'failure', true, vm.$t('Alert'));
                    }
                });

            }
        },
        clientViewTypeList() {
            if (this.clientViewType === "accounts") {
                this.$emit("switchToAccounts");
            }
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Inactive':
                        if (row.status_id == 1001) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'Active / Verified':
                        if (row.status_id == 1002) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'Awaiting Activation/ Not Verified':
                        if (row.user_id != row.parent_id && row.status_code == 'ACT_AWAITED') {
                            action.isVisible = false;
                            action.isDisabled = true;
                        } else {
                            action.isVisible = false;
                            action.isDisabled = false;
                        }
                        break;
                    case 'Activation e-mail pending/ Not Verified':
                        if (row.user_id != row.parent_id && row.status_code == 'CL_PEN') {
                            action.isVisible = true;
                            action.isDisabled = true;
                        } else {
                            action.isVisible = false;
                            action.isDisabled = false;
                        }
                        break;

                    case 'Send/Resend Activation Mail':
                        if (row.user_id != row.parent_id && row.status_id != 1001) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'Add Quotes':
                        if (row.user_id != row.parent_id && row.status_id == 1001) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'Generate/ Reset Password':
                        if (row.user_id != row.parent_id && row.status_id == 1001) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    case 'Configuration':
                        if (row.user_id != row.parent_id) {
                            action.isVisible = true;
                        } else {
                            action.isVisible = false;
                        }
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        ProjectDetail: function (item) {
            var vm = this;
            if (item == undefined) {
                vm.ClientId = 0;
            } else {
                vm.ClientId = item.user_id;
            }
            vm.ProjectDetailPopup = !vm.ProjectDetailPopup;
        },
        actionButtonClick(callbackfunction, item, index, event, isChild) {
            switch (callbackfunction) {
                case 'Inactive':
                    this.UpdateStatusAccount(item, 1002);
                    break;
                case 'ActiveOrVerified':
                    this.UpdateStatusAccount(item, 1001);
                    break;
                case 'ActivationMail':
                    this.SendActivationMail(item.user_id, 'Client');
                    break;
                case 'Configuration':
                    this.ClientConfiguration(item.user_id);
                    break;
                case 'Generateresetpassword':
                    this.SendActivationMail(item.user_id, 'Client');
                    break;
                case 'AddQuotes':
                    this.AddQuotes(item.user_id);
                    break;
                case 'Edit':
                    this.UpdateAccount(item);
                    break;
            }
        },
        AddQuotes: async function (ClientId) {
            var vm = this;
            vm.$router.push({path: `/CRM/ManageQuotation/0/CRM/CRM_QUOTES/0?clientId=${ClientId}`});
            //window.location.href = "/Quotation/Create/0?code=" + 3 + "&clientId=" + ClientId + "";
        },
        openaddAccount: async function (ClientId) {
            var vm = this;
            window.location.href = "/CRM/ManageAccount?id=0&AccountId=" + 1 + "&AccountCode=NEW_CUSTOMER&reqfrom=/client/Index";
        },
        openaddClient: async function (ClientId) {
            var vm = this;
            this.$router.push({ name: 'AddClient' });
        },
        UpdateAccount: async function (item) {
            var vm = this;
            if (item.user_id == item.parent_id) {
                vm.$router.push({ name: 'UpdateAccount', params: { id: item.user_id } });
            } else {
                if (item.parent_id == null) {
                    item.parent_id = 0;
                }
                vm.$router.push({ name: 'UpdateClient', params: { id: item.user_id, parentId: item.parent_id } });
            }
        },
        UpdateStatusAccount: async function (item, status) {
            var vm = this;
            await vm.SendActivationMail(item.user_id, 'UpdateStatus');
            var type = item.user_id == item.parent_id ? 'Account' : 'Client';
            var params = `id=${item.user_id}&statusId=${status}&type=${type}`;
            await DataServices.UpdateStatusAccount(params).then(response => {
                if (response.status == 200) {
                    vm.FetchClientDetails();
                    vm.ShowAlert(vm.$t('ConfigurationSuccessDeActivationMailToCL'), "Success", true, vm.$t('Alert'));
                }
                else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                }
            });
        },
        ClientConfiguration: function (UserId) {
            var vm = this;
            vm.ClientId = UserId;
            vm.ConfigurationPopup = !vm.ConfigurationPopup;
        },
        SendActivationMail: async function (UserId, type) {
            var vm = this;
            var params = `ClientId=${UserId}&from=${type}`;
            await DataServices.SendActivationMail(params).then(response => {
                if (response.status == 200) {
                    if (type == 'Client') {
                        vm.FetchClientDetails();
                        vm.ShowAlert(vm.$t('SuccessActivationMailToCL'), "Success", true, vm.$t('Alert'));
                    }else if(type == 'UpdateStatus'){}
                    else {
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                    }
                }
            });
        },
        extractDynamicValues: function (searchCondition) {
            var vm = this;
            let likeRegex = /(\w+)\s+LIKE\s+'%([^%]+)%'/g;
            let inRegex = /(\w+)\s+in\s+\(([^)]+)\)/g;
            let matches;
            while ((matches = likeRegex.exec(searchCondition)) !== null) {
                let field = matches[1];
                let value = matches[2].trim();
                if (field === 'Account') {
                    vm.AccountName = value;
                } else if (field === 'ClientName') {
                    vm.ClientName = value;
                }
                else if (field === 'Email') {
                    vm.SearchByEmail = value;
                }
                else if (field === 'STATUS_ID') {
                    vm.StatusIds = values;
                }
                else if (field === 'AccountNo') {
                    vm.SearchByAccountNo = value;
                }
                else if (field === 'ContractNo') {
                    vm.SearchByContractNo = value;
                }
                else if (field === 'PhoneNo') {
                    vm.SearchByPhoneNo = value;
                }
            }
            while ((matches = inRegex.exec(searchCondition)) !== null) {
                let field = matches[1];
                let values = matches[2].split(',').map(v => v.replace(/'/g, '').trim());
                if (field === 'STATUS_ID') {
                    vm.StatusIds = values;
                }
            }
            return vm;
        },
        leftCommonSearch: function (value) {
            var vm = this;
            vm.AccountName = '';
            vm.ClientName = '';
            vm.SearchByEmail = '';
            vm.StatusIds = '';
            vm.SearchByAccountNo = '';
            vm.SearchByContractNo = '';
            vm.SearchByPhoneNo = '';
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            var searchCondition = value.searchCondition;
            if (typeof value.searchCondition != 'undefined') {
                vm.extractDynamicValues(searchCondition);
            }
            this.FetchClientDetails();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "openaddAccount":
                    this.openaddAccount(null);
                    break;
                case "openaddClient":
                    this.openaddClient();
                    break;
                case 'ManageView':
                    this.ManageView();
                    break;
            }
        },
        commonDeleteData: async function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                var params = `clientIds=${val}`;
                var childUserIds = '';
                var parentAccountIds = '';
                await DataServices.GetClientdetail(params).then(function (response) {
                    childUserIds = response.data[0].childUserIds;
                    parentAccountIds = response.data[1].parentAccountIds;
                });
                await vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete")), async function () {
                    if (childUserIds) {
                        var url = `ids=${val}&tableName=TALYGEN_company_client&primaryKey=user_id`;
                        await DataServices.Delete(url).then(response => {
                            if (response.status == 200) {
                                vm.Isdeleted = true;
                            }
                            else {
                                vm.Isdeleted = false;
                            }
                        });
                    }
                    if (parentAccountIds) {
                        var url = `ids=${val}&tableName=TALYGEN_account&primaryKey=account_id`;
                        await DataServices.Delete(url).then(response => {
                            if (response.status == 200) {
                                vm.Isdeleted = true;
                            }
                            else {
                                vm.Isdeleted = false;

                            }
                        });
                    }
                    if (vm.Isdeleted == true) {
                        vm.FetchClientDetails();
                        vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Account')]), "Success", true, vm.$t('Alert'));
                    } else {
                        vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Account')]), "failure", true, vm.$t('Alert'));
                    }
                });

            }
            else {
                vm.ShowAlert(vm.$t('Selectanyrecordtodelete', [vm.$t('Account')]), "failure", true, vm.$t('Alert'));
            }
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchClientDetails();
        },
        sortdata: function (sortBy) {
            let sortField = sortBy === 'CLIENT_NAME' ? 'NAME' : sortBy;
            this.SortBy = sortField;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortField;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchClientDetails();
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                case 'ActivationAwaited':
                    returnClass = 'px-2 text-center bg-warning text-white d-block bg-warning';
                    break;
                case 'Pending':
                    returnClass = 'px-2 text-center bg-primary text-white d-block bg-primary';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        FetchClientDetails: async function () {
            var vueObj = this;
            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&searchByName=${vueObj.ClientName}&searchBy=${vueObj.AccountName}&statusIds=${vueObj.StatusIds}&searchByEmail=${vueObj.SearchByEmail}&searchByAccountNo=${vueObj.SearchByAccountNo}&searchByContractNo=${vueObj.SearchByContractNo}&searchByPhoneNo=${vueObj.SearchByPhoneNo}`;
             DataServices.GetDynamicClientListing(url)
                .then(function (response) {
                    if (response.data.result.data != "" && response.data.result.data != null) {
                        const filteredSchema = response.data.result.schema.filter(item => 
                             item.COLUMN_NAME !== 'first_name' && item.COLUMN_NAME !== 'last_name'
                        );
                        vueObj.ClientHeaders = filteredSchema;
                        vueObj.ClientHeaders.forEach((column) => {
                            column.settings = null;
                            if (column.COLUMN_NAME == "Status_Name" ||  column.COLUMN_NAME == "Customer_Name" || column.COLUMN_NAME == "owner_id" || column.COLUMN_NAME == "customer_type_id" || column.COLUMN_NAME == "industry_type_id"  || column.COLUMN_NAME == "jurisdiction_id" || column.COLUMN_NAME == "timezone_id" || column.COLUMN_NAME == "user_phone") {
                                column.settings = {
                                    isInSlot: true,
                                };
                            }
                        });
                        if (response.data.result.data.length > 0) {
                            vueObj.ClientData = response.data.result.data;
                            vueObj.TotalRecords =response.data.result.data[0].TOTAL_RECORDS;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.GetManageViewList();
                            vueObj.noRecord = false;
                        } else {
                            vueObj.noRecord = true;
                            vueObj.ClientData = [];
                            vueObj.TotalRecords = 0
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.ClientData = [];
                        vueObj.TotalRecords = 0
                    }
                })
        }
    }
}
