async function  bindleftCommonSearchdropdown(instance,modulename, filtername) {
    var vm = instance;
    vm.leftSearchFields[3].listOptions=[];
    var url=`statusType=${filtername}&hasglobal=true&langCode=en`
   await DataServices.GetStatusList(url)
        .then(function (response) {
            if (response.data != '') {
                response.data.forEach(item => {
                    if(item.STATUS=="ActivationAwaited"){
                        item.STATUS='Awaiting Activation';
                    }
                    vm.leftSearchFields[3].listOptions.push({
                        name: item.STATUS,
                        value: `${item.STATUS_ID}` 
                    })
                })  
            }
        });
}

export default { buttons, leftsearchSchema,listActions,bindleftCommonSearchdropdown };